.extension-editor {
	height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	margin: 0px auto;
	max-width: 80%;
}

.extension-editor .clickable {
	cursor: pointer;
}

.extension-editor > .header {
	display: flex;
	padding-top: 20px;
	padding-bottom: 14px;
	padding-left: 20px;
	padding-right: 20px;
	overflow: hidden;
	font-size: 14px;
}

.extension-editor > .header > .icon-container {
	position: relative;
}

.extension-editor > .header > .icon-container .icon {
	height: 128px;
	width: 128px;
	object-fit: contain;
}

.extension-editor > .header > .icon-container .extension-remote-badge {
	position: absolute;
	right: 0px;
	top: 88px;
	width: 38px;
	height: 38px;
	line-height: 38px;
	border-radius: 20px;
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
}

.extension-editor > .header > .icon-container .extension-remote-badge .codicon {
	color: currentColor;
	font-size: 28px;
}

.extension-editor > .header > .details {
	padding-left: 20px;
	overflow: hidden;
	user-select: text;
	-webkit-user-select: text;
}

.extension-editor > .header > .details > .title {
	display: flex;
	align-items: center;
}

.extension-editor > .header > .details > .title > .name {
	flex: 0;
	font-size: 26px;
	line-height: 30px;
	font-weight: 600;
	white-space: nowrap;
}

.extension-editor > .header > .details > .title > .name.deprecated {
	text-decoration: line-through;
}

.extension-editor > .header > .details > .title > .version {
	margin-left: 10px;
	user-select: text;
	-webkit-user-select: text;
	white-space: nowrap;
}

.extension-editor > .header > .details > .title > .builtin {
	font-size: 10px;
	font-style: italic;
	margin-left: 10px;
}

.extension-editor > .header > .details > .title > .pre-release {
	margin-left: 10px;
	padding: 0px 4px;
	border-radius: 4px;
	background-color: var(--vscode-extensionIcon-preReleaseForeground);
	color: #ffffff;
	display: flex;
	align-items: center;
}

