	font-size: 120%;
	border-bottom: 1px solid rgba(128, 128, 128, 0.22);
	padding: 3px 6px;
	line-height: 22px;
}

.extension-editor > .body > .content table tr:nth-child(odd) {
	background-color: rgba(130, 130, 130, 0.04);
}

.extension-editor > .body > .content table tr:not(:first-child):hover {
	background-color: rgba(128, 128, 128, 0.15);
}

.extension-editor > .body > .content table th,
.extension-editor > .body > .content table td {
	padding: 2px 16px 2px 4px;
}

.extension-editor > .body > .content table td {
	vertical-align: top;
}

.extension-editor > .body > .content table th:last-child,
.extension-editor > .body > .content table td:last-child {
	padding: 2px 4px;
}

.extension-editor > .body > .content table th {
	text-align: left;
	word-break: keep-all;
}

.extension-editor > .body > .content table td > div,
.extension-editor > .body > .content table td > div > p {
	padding: 0px;
	margin: 0px;
}

.extension-editor code:not(:empty) {
	font-family: var(--monaco-monospace-font);
	font-size: 90%;
	background-color: rgba(128, 128, 128, 0.17);
	border-radius: 4px;
	padding: 1px 4px;
}

.extension-editor > .body > .content table .colorBox {
	box-sizing: border-box;
	width: 0.8em;
	height: 0.8em;
	display: inline-block;
	border-width: 0.1em;
	border-style: solid;
	border-color: rgb(0, 0, 0);
	margin: 0em 0.2em;
	vertical-align: middle;
}

.monaco-workbench.vs-dark .extension-editor > .body > .content table .colorBox,
.monaco-workbench.hc-black
	.extension-editor
	> .body
	> .content
	table
	.colorBox {
	border-color: rgb(238, 238, 238);
}

.extension-editor .subcontent .monaco-list-row .content .unknown-extension {
	line-height: 62px;
}

.extension-editor
	.subcontent
	.monaco-list-row
	.content
	.unknown-extension
	> .error-marker {
	background-color: #be1100;
	padding: 2px 4px;
	font-weight: bold;
	font-size: 11px;
	color: #ccc;
}

.extension-editor .subcontent .monaco-list-row .unknown-extension > .message {
	padding-left: 10px;
	font-weight: bold;
	font-size: 14px;
}

.extension-editor .subcontent .monaco-list-row .extension {
	display: flex;
	align-items: center;
}

.extension-editor .subcontent .monaco-list-row .extension > .details {
	flex: 1;
	overflow: hidden;
	padding-left: 10px;
}

.extension-editor .subcontent .monaco-list-row .extension > .details > .header {
	display: flex;
	align-items: center;
	line-height: 19px;
	overflow: hidden;
}

.extension-editor .subcontent .monaco-list-row .extension > .icon {
	height: 40px;
	width: 40px;
	object-fit: contain;
}

.extension-editor
	.subcontent
	.monaco-list-row
	.extension
	> .details
	> .header
	> .name {
	font-weight: bold;
	font-size: 16px;
}

.extension-editor
	.subcontent
	.monaco-list-row
	.extension
	> .details
	> .header
	> .name:hover {
	text-decoration: underline;
}

.extension-editor
	.subcontent
	.monaco-list-row
	.extension
	> .details
	> .header
	> .identifier {
	font-size: 90%;
	opacity: 0.6;
	margin-left: 10px;
	background: rgba(173, 173, 173, 0.31);
	padding: 0px 4px;
	border-radius: 4px;
}

.extension-editor .subcontent .monaco-list-row .extension > .details > .footer {
	display: flex;
	line-height: 19px;
	overflow: hidden;
	padding-top: 5px;
}

.extension-editor
	.subcontent
	.monaco-list-row
	.extension
	> .details
	> .footer
	> .author {
	font-size: 90%;
	font-weight: 600;
	opacity: 0.6;
}

.extension-editor .extensions-grid-view {
	display: flex;
	flex-wrap: wrap;
}

.extension-editor .extensions-grid-view > .extension-container {
	width: 350px;
	margin: 0 10px 20px 0;
}

.extension-editor .extensions-grid-view .extension-list-item {
	cursor: default;
}

.extension-editor
	.extensions-grid-view
	.extension-list-item
	> .details
	.header
	> .name {
	cursor: pointer;
}

.extension-editor
	.extensions-grid-view
	.extension-list-item
	> .details
	> .header-container
	> .header
	> .version,
.extension-editor
	.extensions-grid-view
	.extension-list-item
	> .details
	> .header-container
	> .header
	> .ratings,
.extension-editor
	.extensions-grid-view
	.extension-list-item
	> .details
	> .header-container
	> .header
	> .install-count {
	display: none;
}

.extension-editor
	.extensions-grid-view
	> .extension-container:focus
	> .extension-list-item
	> .details
	.header
	> .name,
.extension-editor
	.extensions-grid-view
	> .extension-container:hover
	> .extension-list-item
	> .details
	.header
	> .name {
	text-decoration: underline;
}

.monaco-workbench.vs
	.extension-editor
	.extensions-grid-view
	> .extension-container.disabled
	> .extension-list-item
	> .icon-container
	> .icon,
.monaco-workbench.vs-dark
	.extension-editor
	.extensions-grid-view
	> .extension-container.disabled
	> .extension-list-item
	> .icon-container
	> .icon,
.monaco-workbench.vs
	.extension-editor
	.extensions-grid-view
	> .extension-container.disabled
	> .extension-list-item
	> .details
	> .header-container
	.codicon,
.monaco-workbench.vs-dark
	.extension-editor
	.extensions-grid-view
	> .extension-container.disabled
	> .extension-list-item
	> .details
	> .header-container
	.codicon {
	opacity: 0.5;
}

.extension-editor
	> .body
	> .content
	.runtime-status
	.activation-details
	> .activation-element-entry
	> .activation-message-title {
	padding-right: 10px;
}

.extension-editor > .body > .content .runtime-status .message-entry {
	display: flex;
	align-items: center;
	margin: 5px;
}

.extension-editor > .body > .content .runtime-status .message-entry .codicon {
	padding-right: 2px;
}

.monaco-workbench
	.extension-editor
	> .header
	> .details
	> .recommendation
	.codicon {
	color: var(--vscode-extensionButton-prominentBackground);
}

/* Features Tab */

.extension-editor .subcontent.feature-contributions {
	margin-top: 14px;
}

.extension-editor .subcontent.feature-contributions .features-list-container {
	height: 100%;
}

.extension-editor
	.subcontent.feature-contributions
	.features-list-container
	> .features-list-wrapper {
	height: 100%;
	padding-left: 24px;
}

.extension-editor
	.subcontent.feature-contributions
	.features-list-container
	> .features-list-wrapper
