	opacity: 0.8;
	font-size: 12px;
}

.extension-editor
	.subcontent.feature-contributions
	.features-list-container
	> .features-list-wrapper
	.monaco-list-row.extension-feature-list-item
	.extension-feature-status {
	padding-left: 5px;
}

.extension-editor .subcontent.feature-contributions .feature-view-container {
	height: 100%;
}

.extension-editor
	.subcontent.feature-contributions
	.extension-feature-content
	.feature-body-content
