	height: 100%;
	box-sizing: border-box;
}

.extension-editor
	.subcontent.feature-contributions
	.extension-feature-content
	.feature-header {
	margin: 0 10px 10px 0;
	display: flex;
	line-height: 20px;
	align-items: center;
